<template>
  <div class="purchase">
    <v-loader v-if="isLoading" />
    <template v-else>
      <img class="icon" :src="purchase.product.icon_uri" alt="" aria-hidden />

      <h1 class="heading">{{ $jt(purchase.product.name_json) }}</h1>

      <p class="text">{{ $jt(purchase.product.description_json) }}</p>

      <p v-html="purchase.notice" class="text"></p>

      <p class="text text_margin-sm">
        <Icon icon="tabler:tags" class="text__icon" />
        <span>{{ purchase.product.type_translated }}</span>
      </p>

      <p v-if="purchase.renews_at" class="text text_margin-sm">
        <Icon icon="tabler:calendar-repeat" class="text__icon" />
        <span>{{ $t('SUBSCRIPTION_RENEWS_ON_DATE', { date: new Date(purchase.renews_at * 1000).toLocaleDateString() })
          }}</span>
      </p>

      <p v-if="isSubscription" class="text text_warning text_margin-sm">
        <Icon icon="tabler:exclamation-circle-filled" class="text__icon" />
        <html-with-links tag="span"
          :html="$t('SUBSCRIPTION_CAN_BE_CANCELED_ANYTIME', { subscriptionsLink: '/my/billing?subscriptions' })" />
      </p>

      <p v-if="isPerDevice" class="text text_warning text_margin-sm">
        <Icon icon="tabler:exclamation-circle-filled" class="text__icon" />
        <html-with-links tag="span"
          :html="$t('PURCHASE_WILL_APPLY_ONLY_TO_DEVICE', { device: purchase.device_model })" />
      </p>

      <p class="text text_margin-sm">
        {{ $t('PRICE') }}: <b>{{ formatCurrency(purchase.product.price_cents_eur) }}</b>
      </p>
      <p v-if="purchase.status === 'new'" class="text text_margin-sm">
        {{ $t('PURCHASE_DUE_NOW') }}: <b>{{ formatCurrency(purchase.due_cents_eur) }}</b>
      </p>

      <p v-if="purchase.status === 'ok'" class="text text_success text_margin-sm">
        <Icon icon="tabler:circle-check-filled" class="text__icon" />
        <span>{{ $t('PURCHASE_STATUS_OK_MESSAGE') }}</span>
      </p>

      <v-checkbox v-if="mustAcceptAgreement" v-model="isAgreementAccepted" class="agreement">
        <i18next path="PURCHASE_AGREE_WRAPPER" tag="span">
          <button class="g-pseudo-link" place="tos" @click="showTos">{{ $t('PURCHASE_AGREE_TOS_PART') }}</button>
        </i18next>
      </v-checkbox>
      <div v-else-if="purchase.product.tos_text" class="text text_margin-sm">
        <button @click="showTos" class="g-pseudo-link">{{ $t('TERMS OF SERVICE') }}</button>
      </div>


      <div class="controls">
        <v-button
          :tag="purchase.redirect_uri ? 'a' : 'button'"
          :href="purchase.redirect_uri"
          type="primary"
          size="lg"
          @click.native="onSubmitButtonClick"
        >
          {{ submitButtonLabel }}
        </v-button>
        <v-button v-if="purchase.next_action !== 'return_to_app'" tag="a" :href="cancelUrl" type="negative" size="lg">
          {{ $t('CANCEL') }}
        </v-button>
      </div>

      <v-modal name="tos" :width="500" close-button>
        <p class="tos-text">
          {{ purchase.product.tos_text }}
        </p>
      </v-modal>
    </template>
  </div>
</template>

<script>
import { Icon } from '@iconify/vue2';
import VButton from '@/components/Button';
import HtmlWithLinks from '@/components/HtmlWithLinks';
import VLoader from '@/components/Loader';
import { APIError } from '@/plugins/Api';
import formatCurrency from '@/utils/formatCurrency';
import VCheckbox from '@/components/Checkbox';
import VModal from '@/components/VModal.vue';

export default {
  components: {
    Icon,
    VButton,
    HtmlWithLinks,
    VLoader,
    VCheckbox,
    VModal,
  },
  data() {
    return {
      isLoading: false,
      isProcessing: false,
      isAgreementAccepted: false,
      purchase: {
        product: {
          identifier: '',
          name_json: '',
          description_json: '',
          type: '',
          type_translated: '',
          price_cents_eur: undefined,
          app_identifier: '',
          amount: undefined,
          icon_uri: '',
          tos_text: '',
          license_type: '',
        },
        redirect_uri: '',
        next_action: '',
        renews_at: undefined,
        device_model: '',
        status: '',
        due_cents_eur: undefined,
        notice: '',
      },
    };
  },
  computed: {
    title() {
      return this.$jt(this.purchase.product.name_json);
    },
    purchaseTicket() {
      return this.$route.query.purchaseTicket;
    },
    appIdentifier() {
      return this.$route.query.appIdentifier;
    },
    isSubscription() {
      return this.purchase.product.type.includes('subscription');
    },
    isPerDevice() {
      return this.purchase.product.license_type === 'per_device';
    },
    mustAcceptAgreement() {
      return this.purchase.product.tos_text && this.purchase.status === 'new';
    },
    submitButtonLabel() {
      switch (this.purchase.next_action) {
        case 'confirm':
          return this.$t('CONFIRM_PURCHASE');
        case 'redirect':
          return this.$t('PROCEED_TO_PAYMENT');
        case 'return_to_app':
        default:
          return this.$t('RETURN_TO_APP');
      }
    },
    cancelUrl() {
      return `appdb.${this.appIdentifier}://verifyPurchase=cancelled`;
    },
  },
  methods: {
    formatCurrency,
    async loadPurchase() {
      this.isLoading = true;

      try {
        const purchase = await this.$api.processPurchase({
          purchaseTicket: this.purchaseTicket,
          appIdentifier: this.appIdentifier,
        });

        this.purchase = purchase;
      } catch (e) {
        this.$router.replace({
          name: 'error',
          params: {
            message: e instanceof APIError ? e.translatedMessage : e.message,
          },
        });
      } finally {
        this.isLoading = false;
      }
    },
    async onSubmitButtonClick(event) {
      if (this.mustAcceptAgreement && !this.isAgreementAccepted) {
        event.preventDefault();
        this.$notify({
          type: 'error',
          text: this.$t('PURCHASE_AGREEMENT_REQUIRED'),
          position: 'top left',
        });
        return;
      }

      if (this.purchase.redirect_uri) {
        return;
      }

      this.isProcessing = true;
      try {
        this.purchase = await this.$api.processPurchase({
          operation: this.purchase.next_action,
          purchaseTicket: this.purchaseTicket,
          appIdentifier: this.appIdentifier,
        });
      } finally {
        this.isProcessing = false;
      }
    },
    showTos() {
      this.$modal.show('tos');
    },
  },
  created() {
    this.loadPurchase();
  },
};
</script>

